<template>
	 <v-container fluid class="formulaire">
	 	<div style="height:">	



    <v-layout row wrap style="margin-top:30px ">
      
    	<v-flex md3 >
    		 <v-subheader>Tranche écart tot.</v-subheader>
    	</v-flex>
    	<v-flex md3 >
    		<v-text-field
      label="tranche d'écart"
      v-model="name"
      
      
      required
    ></v-text-field>
    	</v-flex>
    	
    </v-layout>

     <v-layout row wrap style="margin-top:-15px " >
    	<v-flex md3 >
    		 <v-subheader>Résultats</v-subheader>
    	</v-flex>
    	
    	<v-flex md3 >
    		<v-select
          :items="items"
          
          label="résultats"
          item-value="text"
        ></v-select>
    	</v-flex>
    	
    </v-layout>

 <v-layout row wrap style="margin-top:-15px " >
      <v-flex md3 >
         <v-subheader>Technicien</v-subheader>
      </v-flex>
      
      <v-flex md3 >
        <v-select
          :items="items"
          
          label="technicien"
          item-value="text"
        ></v-select>
      </v-flex>
      
    </v-layout>


</div>





</v-container>
</template>


<script>
 
  export default {
    

    data: () => ({
      name: '',
      email: '',
      select: null,
      items: [
        'Item 1',
        'Item 2',
        'Item 3',
        'Item 4'
      ],
      checkbox: false
    }),

   


  }
</script>


<style>
.input-group--text-field input, .input-group--text-field textarea{
	font-size: 12px !important;
}

.list__tile__title{
	font-size: 12px !important;
}
	
	.formulaire .input-group{
		padding:12px 0 0 !important;
	}

	.input-group--select .input-group__selections__comma{
		font-size: 12px !important;
	}

	label{
		font-size: 12px !important;
	}

</style>


