<template>
  <v-app style="background-color:#e0e0e0 !important">
   
    <v-navigation-drawer style="background-color:#003b70" dark persistent :mini-variant="miniVariant" :clipped="clipped" v-model="drawer" enable-resize-watcher fixed app >

    <div style="width:300px; height: 200px ">
      <img style="width:100%; height: 200px;" src="../src/img/fond1.jpg">    
    </div>

    <v-list style=" ; margin-top:-56px; ">
      <v-list-group >
        <v-list-tile slot="activator">
          <v-list-tile-action>
               <v-list-tile-avatar>
          <img  src="https://randomuser.me/api/portraits/men/85.jpg" >
        </v-list-tile-avatar>
            </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>John Doe</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-list-tile @click.native.stop="dialog = true" style="background-color:#002458">
          <v-list-tile-action>
            <v-icon  style="color:#fff">person</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>paramètre</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-list-tile  @click="" style="background-color:#002458">
          <v-list-tile-action>
            <v-icon  style="color:#fff">settings</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>mon compte</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-list-tile  @click="" style="background-color:#002458">
          <v-list-tile-action>
            <v-icon  style="color:#fff">lock_outline</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>log out</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list-group>
    </v-list>






       <v-list>
          <v-list-tile avatar v-for="item in items" :key="item.title" router :to="item.link" @click="">
            <v-list-tile-avatar>
              <v-icon :class="[item.iconClass]">{{ item.icon }}</v-icon>
            </v-list-tile-avatar>
            <v-list-tile-content>
              <v-list-tile-title>{{ item.title }}</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
      </v-list>
    </v-navigation-drawer>
   
    <v-toolbar color="primary" dark  app :clipped-left="clipped">
      <v-toolbar-side-icon @click.stop="miniVariant = !miniVariant"></v-toolbar-side-icon>
      <v-toolbar-title > <a href="home"><img  style="width:50%;margin-top:10px" src="../src/img/logoEngie.png"></a></v-toolbar-title>
      <v-spacer></v-spacer>
      <v-text-field light prepend-icon="search" solo append-icon="cancel" hide-details single-line></v-text-field>
    </v-toolbar>
    
    <v-content>
      <router-view/>
    </v-content>




 


  </v-app>
</template>

<script>

import toolbar from "./components/toolbar"; 
import crintervention from "./components/crintervention"; 
export default {
  components:{
    toolbar,
    crintervention
  },
 
  data () {
    return {
      dialog: false,
      clipped: false,
      drawer: true,
      fixed: false, 
      miniVariant: false,
       items: [
          { icon: 'home', title: 'vue 1', link:'../pageun' },
          { icon: 'room', title: 'vue 2', link:'../pagedeux'},
          { icon: 'shop', title: 'vue 3', link:'../intervention' },
          { icon: 'shop', title: 'vue 4', link:'../listeinstallation' },
          { icon: 'shop', title: 'vue 5', link:'../elementvariable' }
        ],
    }
  },
  name: 'App'
}
</script>

<style>
.list__tile:hover, .list__tile:active {
  color: #FF9800 !important;
}
</style>




