<template>


  <v-layout row class="ml-2 mr-2">
   <v-container fluid grid-list-md>
     <breadcrumb></breadcrumb>
      <v-layout row > 
        <v-flex xs12 sm12 >
          <v-card>
             <v-card-title class="soustitre">
        <i class="material-icons mr-2 roundicon">person</i> Intervenants
      </v-card-title>
            <v-layout row wrap class="mr-3 ml-3">
               <v-flex xs6 md6 >
                 <v-text-field light prepend-icon="search" placeholder="I44413 - BORDEL Laurent" solo append-icon="cancel" hide-details single-line></v-text-field>    
                </v-flex>
                 <v-flex xs2 md2 style="text-align: right !important">
                  <v-subheader  v-text="'Organisation :'"></v-subheader>
                 </v-flex>
                <v-flex xs4 md4 >

                 <v-text-field  placeholder="SDT - COF CLERMONT" light solo hide-details single-line></v-text-field>    
                </v-flex>
            </v-layout>
            <v-card-actions>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>



        <v-layout row wrap class="mt-2">  
            <v-flex d-flex md12 xs12>
            
            <interventions ></interventions>
          </v-flex>
        </v-layout>
    </v-container>
  </v-layout>
</template>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<script>


  

  import interventions from "../components/interventions";  
  import breadcrumb from "../components/breadcrumb"; 
  export default {
     components: {
    interventions,
    breadcrumb
  },
    data () {

      return {
         active: null,
       
      }
    },
     methods: {
      next () {
        const active = parseInt(this.active)
        this.active = (active < 2 ? active + 1 : 0).toString()
      }
    }
  }
</script>
 


