<template>
  <v-layout row class="ml-2 mr-2">
   <v-container fluid grid-list-md>
     <breadcrumb></breadcrumb>
      <v-layout row > 


 <v-flex xs12 sm12 >
          <v-card >
        
<v-layout row wrap class="mr-3 ml-3">

      <v-flex xs6 >
      <v-menu
        ref="menu"
        lazy
        :close-on-content-click="false"
        
        transition="scale-transition"
        offset-y
        full-width
        :nudge-right="40"
        min-width="290px"
        
      >
        <v-text-field
          slot="activator"
          label="Selection de la semaine"
          
          prepend-icon="event"
          readonly
        ></v-text-field>
        <v-date-picker    scrollable>
          <v-spacer></v-spacer>
          <v-btn flat color="error" @click="menu = false">Cancel</v-btn>
          <v-btn flat color="success" @click="$refs.menu.save(date)">OK</v-btn>
        </v-date-picker>
      </v-menu>
    </v-flex>


   
  </v-layout>















          </v-card>
        </v-flex>
      </v-layout>




 <v-layout row > 
        <v-flex xs12 sm12 >
          <v-card class="mt-3">
          <v-card-title class="soustitre">
        <i class="material-icons mr-2 roundicon">query_builder</i> Heures programmées
      </v-card-title>
            <heureprogrammee></heureprogrammee>
          </v-card>
        </v-flex>
      </v-layout>



        <v-layout row wrap class="mt-3">  
            <v-flex d-flex md12 xs12>
               <v-card >
            <v-card-title class="soustitre">
        <i class="material-icons mr-2 roundicon">restore</i> Heures exceptionnelles
      </v-card-title>
            <heureexceptionnelle ></heureexceptionnelle>
          </v-card>
          </v-flex>
        </v-layout>
    </v-container>
  </v-layout>
</template>

<script>
import heureprogrammee from "../components/heureprogrammee";  
import breadcrumb from "../components/breadcrumb";
import heureexceptionnelle from "../components/heureexceptionnelle";  
  export default {
     components: {
   heureprogrammee,
   breadcrumb,
   heureexceptionnelle
  },
    data () {

      return {
         active: null,
      }
    },
     
  }
</script>


