<template >

  <v-card >
    <v-card-title class="soustitre">
        <i class="material-icons mr-2 roundicon">place</i> Interventions
      </v-card-title>

    <v-layout row wrap class="mr-3 ml-3">
      <v-flex xs2 >
        <v-menu
          ref="menu"
          lazy
          :close-on-content-click="false"
          
          transition="scale-transition"
          offset-y
          full-width
          :nudge-right="40"
          min-width="290px"
          
        >
          <v-text-field
            slot="activator"
            label="Date de début"
            
            prepend-icon="event"
            readonly
          ></v-text-field>
          <v-date-picker v-model="picker"   scrollable>
            <v-spacer></v-spacer>
            <v-btn flat color="error" @click="menu = false">Cancel</v-btn>
            <v-btn flat color="success" @click="$refs.menu.save(date)">OK</v-btn>
          </v-date-picker>
        </v-menu>
      </v-flex>


      <v-flex xs2 >
        <v-menu
          ref="menu"
          lazy
          :close-on-content-click="false"
          
          transition="scale-transition"
          offset-y
          full-width
          :nudge-right="40"
          min-width="290px"
          
        >
          <v-text-field
            slot="activator"
            label="Date de fin"
            
            prepend-icon="event"
            readonly
          ></v-text-field>
          <v-date-picker v-model="picker2"  scrollable>
            <v-spacer></v-spacer>
            <v-btn flat color="error" @click="menu = false">Cancel</v-btn>
            <v-btn flat color="success" @click="$refs.menu.save(date)">OK</v-btn>
          </v-date-picker>
        </v-menu>
      </v-flex>


      <v-flex xs2 class="pl-5">
         <v-select
            :items="selectlist"
            v-model="e1"
            label="semaines"
            single-line
          ></v-select>
      </v-flex>
      <v-flex xs2>
      </v-flex>
   
    </v-layout>



 <template>
  <div>
    <v-dialog v-model="dialog" max-width="700px">
      <v-btn color="warning" dark slot="activator" class="mb-3 ml-3"><i class="material-icons mr-3">add</i> Ajouter une intervention</v-btn>
      <v-card>
        <v-card-title>
          <span class="headline">{{ formTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12 sm6 md3>
                <v-text-field label="Interventions" v-model="editedItem.name"></v-text-field>
              </v-flex>
              <v-flex xs12 sm6 md3>
                <v-text-field label="Chantier" v-model="editedItem.calories"></v-text-field>
              </v-flex>
              <v-flex xs12 sm6 md3>
                <v-text-field label="Libellé" v-model="editedItem.fat"></v-text-field>
              </v-flex>
              <v-flex xs12 sm6 md3>
                <v-text-field label="A faire" v-model="editedItem.carbs"></v-text-field>
              </v-flex>
              <v-flex xs12 sm6 md3>
                <v-text-field label="Libellé" v-model="editedItem.protein"></v-text-field>
              </v-flex>
              <v-flex xs12 sm6 md3>
                <v-text-field label="Prestation" v-model="editedItem.prestation"></v-text-field>
              </v-flex>
              <v-flex xs12 sm6 md3>
                <!-- <v-text-field label="Date" v-model="editedItem.date"></v-text-field> -->
                <v-menu
          ref="menu"
          lazy
          :close-on-content-click="false"
          
          transition="scale-transition"
          offset-y
          full-width
          :nudge-right="40"
          min-width="290px"
          
        >
          <v-text-field
            slot="activator"
            label="Date"
             v-model="editedItem.date"
            prepend-icon="event"
            readonly
          ></v-text-field>
          <v-date-picker v-model="picker"   scrollable>
            <v-spacer></v-spacer>
            <v-btn flat color="error" @click="menu = false">Cancel</v-btn>
            <v-btn flat color="success" @click="$refs.menu.save(date)">OK</v-btn>
          </v-date-picker>
        </v-menu>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" flat @click.native="close">Cancel</v-btn>
          <v-btn color="blue darken-1" flat @click.native="save">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-data-table
      :headers="headers"
      :items="items"
      hide-actions
      class="elevation-1"
    >
      <template slot="items" slot-scope="props">
        <td>{{ props.item.name }}</td>
        <td class="text-xs-right">{{ props.item.calories }}</td>
        <td class="text-xs-right">{{ props.item.fat }}</td>
        <td class="text-xs-right">{{ props.item.carbs }}</td>
    <!--     <td class="text-xs-right">{{ props.item.protein }}</td> -->
        <td class="text-xs-right">{{ props.item.prestation }}</td>
        <td class="text-xs-right">{{ props.item.date }}</td>
        <td class="justify-center layout px-0">
          <v-btn icon class="mx-0" @click="editItem(props.item)">
            <v-icon color="info">edit</v-icon>
          </v-btn>
          <v-btn icon class="mx-0" @click="deleteItem(props.item)">
            <v-icon color="error">delete</v-icon>
          </v-btn>
        </td>
      </template>

      <template slot="no-data">
        <v-btn color="primary" @click="initialize">Reset</v-btn>
      </template>
      

    </v-data-table>
  </div>



</template>


<v-spacer></v-spacer>

<interventionform></interventionform>

<div class="ml-2 mb-2"> 
 <v-btn color="success">Ok</v-btn>
    <v-btn color="error">Annuler</v-btn>
    <v-btn color="warning">Mesure</v-btn>
    <v-btn color="info">Valider</v-btn>
     <v-btn disabled>Demande Client</v-btn>
     </div>
</v-card>

</template>





  <script>
    import interventionform from "../components/interventionform"
  export default {
    components:{
      interventionform,
    },
    data: () => ({
        
        
       picker: null,
         picker2: null,
        loading3: false,
         e1: null,
         selectlist: [
          { text: 'Janvier' },
          { text: 'Février' },
          { text: 'Mars' },
          { text: 'Avril' },
          { text: 'Mai' },
          { text: 'Juin' },
          { text: 'Juillet' }
        ],
        search: '',
        selected: [],
        
      dialog: false,
      headers: [
        {
          text: 'Installation',
          align: 'left',
          sortable: false,
          value: 'name'
        },
        { text: 'Chantier', value: 'calories' },
        { text: 'Libellé', value: 'fat' },
        { text: 'A faire', value: 'carbs' },
     /*   { text: 'Libellé', value: 'protein' },*/
        { text: 'Prestation', value: 'prestation' },
        { text: 'Date', value: 'date' },
        { text: 'Actions', value: 'name', sortable: false }
      ],
      items: [],
      editedIndex: -1,
      editedItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0
      },
      defaultItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0
      }
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      }
    },

    watch: {
      dialog (val) {
        val || this.close()
      }
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
        this.items = [
          {
            name: 'XXX',
            calories: "xxx",
            fat: "xxx",
            carbs: "xxx",
            protein: "xxx",
            prestation:"xxx",
            date:"10/10/2010"
          },
          {
            name: 'XXX',
            calories: "xxx",
            fat: "xxx",
            carbs: "xxx",
            protein: "xxx",
            prestation:"xxx",
            date:"10/10/2010"
          },
          {
            name: 'XXX',
            calories: "xxx",
            fat: "xxx",
            carbs: "xxx",
            protein: "xxx",
            prestation:"xxx",
            date:"10/10/2010"
          }
        ]
      },

      editItem (item) {
        this.editedIndex = this.items.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        const index = this.items.indexOf(item)
        confirm('Are you sure you want to delete this item?') && this.items.splice(index, 1)
      },

      close () {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.items[this.editedIndex], this.editedItem)
        } else {
          this.items.push(this.editedItem)
        }
        this.close()
      }
    }
  }
</script>




<style>
  .table__overflow{
    overflow-x: hidden;
  }
</style>





