    <v-dialog v-model="dialog" max-width="490">
      <v-card>
        <crintervention></crintervention>
  
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
import crintervention from "../components/crintervention";  

  export default {
     components: {
   crintervention,
  },
    data () {

      return {
         dialog: false,
      }
    },
     
  }
</script>




