<template>
  <v-container fluid>
    <v-layout row wrap>

      <v-flex xs6>
        <v-subheader>Type de congès</v-subheader>
      </v-flex>
      <v-flex xs6>
        <v-select
          :items="items"
          v-model="e1"
          label="Type de congès"
          single-line
        ></v-select>
      </v-flex>
    </v-layout>

<v-layout row wrap>
 <v-flex xs6 >
        <v-subheader>Date de début</v-subheader>
      </v-flex>
      <v-flex xs6 >
      <v-menu
        ref="menu"
        lazy
        :close-on-content-click="false"
        v-model="menu"
        transition="scale-transition"
        offset-y
        full-width
        :nudge-right="40"
        min-width="290px"
        :return-value.sync="date"
      >
        <v-text-field
          slot="activator"
          label="Picker in menu"
          v-model="date"
          prepend-icon="event"
          readonly
        ></v-text-field>
        <v-date-picker v-model="date" no-title scrollable>
          <v-spacer></v-spacer>
          <v-btn flat color="primary" @click="menu = false">Cancel</v-btn>
          <v-btn flat color="primary" @click="$refs.menu.save(date)">OK</v-btn>
        </v-date-picker>
      </v-menu>
    </v-flex>
</v-layout>

<v-layout row wrap>
<v-flex xs6>
        <v-subheader>Date de fin</v-subheader>
      </v-flex>
      <v-flex xs6>
      <v-menu
        ref="menu"
        lazy
        :close-on-content-click="false"
        v-model="menu"
        transition="scale-transition"
        offset-y
        full-width
        :nudge-right="40"
        min-width="290px"
        :return-value.sync="date"
      >
        <v-text-field
          slot="activator"
          label="Picker in menu"
          v-model="date"
          prepend-icon="event"
          readonly
        ></v-text-field>
        <v-date-picker v-model="date" no-title scrollable>
          <v-spacer></v-spacer>
          <v-btn flat color="primary" @click="menu = false">Cancel</v-btn>
          <v-btn flat color="primary" @click="$refs.menu.save(date)">OK</v-btn>
        </v-date-picker>
      </v-menu>
    </v-flex>
    </v-layout>


<v-layout row wrap>
<v-flex xs6 >
        <v-subheader></v-subheader>
      </v-flex>
      <v-flex xs6 >
    <v-checkbox
      v-model="checkbox"
      value="1"
      label="Demi-journée"
      data-vv-name="checkbox"
      type="checkbox"
      required
    ></v-checkbox>
  </v-flex>
</v-layout>


<v-layout row wrap>
<v-flex xs6 >
   <v-subheader>Télécharger un justificatif</v-subheader>
</v-flex>
<v-flex xs6>
 <v-btn
      :loading="loading3"
      @click.native="loader = 'loading3'"
      :disabled="loading3"
      color="blue-grey"
      class="white--text"
    >
      Upload
      <v-icon right dark>cloud_upload</v-icon>
    </v-btn>
  </v-flex>


<v-btn color="warning" class="mt-3" style="width:100%">Consulter les congès des autres collègues</v-btn>

      
     
 <v-layout row>
     
      <v-flex xs>
        <v-text-field
          name="input-7-1"
          label="Commentaire"
          multi-line
        ></v-text-field>
      </v-flex>
    </v-layout>
   



    </v-layout>
  </v-container>
</template>

<script>
  export default {
    data () {
      return {
        e1: null,
        e2: null,
        e3: null,
        e4: null,
        items: [
          { text: 'RTT' },
          { text: 'Congès payé' },
          { text: 'Arret maladie' },
          { text: 'Congès parental' },
          { text: 'Congès maternité' },
          { text: 'Congès anticipé' }
        ],
        states: [
          'Alabama', 'Alaska', 'American Samoa', 'Arizona',
          'Arkansas', 'California', 'Colorado', 'Connecticut',
          'Delaware', 'District of Columbia', 'Federated States of Micronesia',
          'Florida', 'Georgia', 'Guam', 'Hawaii', 'Idaho',
          'Illinois', 'Indiana', 'Iowa', 'Kansas', 'Kentucky',
          'Louisiana', 'Maine', 'Marshall Islands', 'Maryland',
          'Massachusetts', 'Michigan', 'Minnesota', 'Mississippi',
          'Missouri', 'Montana', 'Nebraska', 'Nevada',
          'New Hampshire', 'New Jersey', 'New Mexico', 'New York',
          'North Carolina', 'North Dakota', 'Northern Mariana Islands', 'Ohio',
          'Oklahoma', 'Oregon', 'Palau', 'Pennsylvania', 'Puerto Rico',
          'Rhode Island', 'South Carolina', 'South Dakota', 'Tennessee',
          'Texas', 'Utah', 'Vermont', 'Virgin Island', 'Virginia',
          'Washington', 'West Virginia', 'Wisconsin', 'Wyoming'
        ]
      }
    }
  }
</script>


