<template>
	<v-card>
	<v-card-title class="headline" style="color:#0097c1">
		<i class="material-icons mr-3 " >chrome_reader_mode</i> Compte rendu d'intervention
	</v-card-title>
	 <v-divider></v-divider>
        <v-card-text>
        	<v-card-title class="soustitre">
				<i class="material-icons mr-2 roundicon">list</i> Détail de l'intervention
			</v-card-title>
        	<v-container fluid>
			    <v-layout row wrap>
			      <v-flex xs6 style="margin-top:-15px !important">
			        <v-subheader >Statut</v-subheader>
			      </v-flex>
			      <v-flex xs6>
			        <v-switch :label="`Démarrée: ${switch1.toString()}`" v-model="switch1">
        			</v-switch>
			      </v-flex>			     
			      <v-flex xs6>
			        <v-subheader>Début d'intervention </v-subheader>
			      </v-flex>
			      <v-flex xs6>
			        <v-menu
			        ref="menu"
			        lazy
			        :close-on-content-click="false"
			        
			        transition="scale-transition"
			        offset-y
			        full-width
			        :nudge-right="40"
			        max-width="290px"
			        min-width="290px"
			        :return-value.sync="time"
			      >
			        <v-text-field
			          slot="activator"
			          label="Picker in menu"
			          v-model="time"
			          prepend-icon="access_time"
			          readonly
			        ></v-text-field>
			        <v-time-picker v-model="time" @change="$refs.menu.save(time)"></v-time-picker>
			      </v-menu>
			      </v-flex>
			      <v-flex xs6>
			        <v-subheader>Prestation</v-subheader>
			      </v-flex>
			      <v-flex xs6>
			        <v-select
			          :items="items"
			          v-model="e1"
			          label="Select"
			          single-line
			        ></v-select>
			      </v-flex>		
			      
			    </v-layout>
			     <v-divider></v-divider>
			     <v-flex xs12>
        <v-text-field 
                name="input-1"
                label="Observation"
                textarea

                
              ></v-text-field>
      </v-flex>
    </v-container>


    <v-card-title class="soustitre">
		<i class="material-icons mr-2 roundicon ">list</i> Rapprochement
	</v-card-title>
	<v-container fluid>
		<v-layout row wrap>
			<v-flex xs6>
				<v-text-field light prepend-icon="search" placeholder="N° d'intervention" solo  hide-details single-line>
				</v-text-field> 
			</v-flex>
			<v-flex xs6 class='pl-3'>
				<v-menu
        ref="menu"
        lazy
        :close-on-content-click="false"
        
        transition="scale-transition"
        offset-y
        full-width
        :nudge-right="40"
        min-width="290px"
        
      >
        <v-text-field
          slot="activator"
          label="Date"
          
          prepend-icon="event"
          readonly
        ></v-text-field>
        <v-date-picker  no-title scrollable>
          <v-spacer></v-spacer>
          <v-btn flat color="primary" @click="menu = false">Cancel</v-btn>
          <v-btn flat color="primary" @click="$refs.menu.save(date)">OK</v-btn>
        </v-date-picker>
      </v-menu>
			</v-flex>
			
		</v-layout>
	</v-container>

			  

        	
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error "   @click.native="dialog = false">Supprimer</v-btn>
          <v-btn color="warning"  @click.native="dialog = false">Fin partielle</v-btn>
           <v-btn color="success"  @click.native="dialog = false">Fin totale</v-btn>
        </v-card-actions>
        </v-card>
</template>



<script>
  export default {
    data () {
      return {
        e1: null,
        e2: null,
        e3: null,
        e4: null,
         time: null,
        menu2: false,
        modal2: false,
        switch1: true,
        items: [
          { text: 'State 1' },
          { text: 'State 2' },
          { text: 'State 3' },
          { text: 'State 4' },
          { text: 'State 5' },
          { text: 'State 6' },
          { text: 'State 7' }
        ],
        states: [
          'Alabama', 'Alaska', 'American Samoa', 'Arizona',
          'Arkansas', 'California', 'Colorado', 'Connecticut',
          'Delaware', 'District of Columbia', 'Federated States of Micronesia',
          'Florida', 'Georgia', 'Guam', 'Hawaii', 'Idaho',
          'Illinois', 'Indiana', 'Iowa', 'Kansas', 'Kentucky',
          'Louisiana', 'Maine', 'Marshall Islands', 'Maryland',
          'Massachusetts', 'Michigan', 'Minnesota', 'Mississippi',
          'Missouri', 'Montana', 'Nebraska', 'Nevada',
          'New Hampshire', 'New Jersey', 'New Mexico', 'New York',
          'North Carolina', 'North Dakota', 'Northern Mariana Islands', 'Ohio',
          'Oklahoma', 'Oregon', 'Palau', 'Pennsylvania', 'Puerto Rico',
          'Rhode Island', 'South Carolina', 'South Dakota', 'Tennessee',
          'Texas', 'Utah', 'Vermont', 'Virgin Island', 'Virginia',
          'Washington', 'West Virginia', 'Wisconsin', 'Wyoming'
        ]
      }
    }
  }
</script>

<style>
	.roundicon{
		background-color: #0097c1 !important;
		color:white !important;
		border-radius: 50px !important;
		padding:3px;
	}

	.soustitre{
		font-size: 18px;
	}
</style>




