 <template>
 	<v-toolbar color="primary" dark  app :clipped-left="clipped">
      <v-toolbar-side-icon @click.stop="miniVariant = !miniVariant"></v-toolbar-side-icon>
      <v-toolbar-title > <img style="width:50%;margin-top:10px" src="../img/logoEngie.png"></v-toolbar-title>
      <v-spacer></v-spacer>
      <v-text-field light prepend-icon="search" solo append-icon="cancel" hide-details single-line></v-text-field>
    </v-toolbar>
</template>

<script>
export default { 
  data () {
    return {
      clipped: false,
      drawer: true,
      fixed: false,          
      miniVariant: false
    }
  },
  name: 'App'
}
</script>





