<template>
  <v-layout row class="ml-2 mr-2">
   <v-container fluid grid-list-md>
     
      <v-layout row > 
		<v-flex xs12 sm12 >
	      <v-card>
	       <v-tabs   style="width:100%"
	      v-model="active"
	      color="primary"
	      dark
	      slider-color="secondary">
	      <v-tab
	        :key="1"
	        ripple>
	        <v-icon  class="mr-2">input</v-icon> Périmètre
	      </v-tab>
	      <v-tab
	        :key="2"
	        ripple>
	       <v-icon  class="mr-2">list</v-icon> Critère d'analyse
	      </v-tab>
	      <v-tab-item :key="1" >
	        <v-card flat>
	         <perimetre></perimetre>
	        </v-card>
	      </v-tab-item>

       <v-tab-item :key="2" >
        <v-card flat>
         <critere></critere>
        </v-card>
      </v-tab-item>
    </v-tabs>
      </v-card>
    </v-flex>

</v-layout>
        <!-- 3eme bloc -->
        <v-layout row wrap class="mt-2">  
        <v-flex d-flex md12 xs12>
        
        <vision ></vision>
      </v-flex>
    </v-layout>
        
    </v-container>


  </v-layout>
</template>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<script>


  import newconges from "../components/newconges"; 
  import perimetre from "../components/perimetre"; 
  import critere from "../components/critere"; 
  import vision from "../components/vision";  
  export default {
     components: {
    newconges,
    perimetre,
    vision,
    critere
  },
    data () {

      return {
         active: null,
      }
    },
     methods: {
      next () {
        const active = parseInt(this.active)
        this.active = (active < 2 ? active + 1 : 0).toString()
      }
    }
  }
</script>



