import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import pageun from '@/view/pageun'
import pagedeux from '@/view/pagedeux'
import listeinstallation from '@/view/listeinstallation'
import elementvariable from '@/view/elementvariable'
import intervention from '@/view/intervention'

Vue.use(Router)

export default new Router({
    routes: [{
            path: '/home',
            name: 'home',
            component: home
        },
        {
            path: '/pageun',
            name: 'pageun',
            component: pageun
        },
        {
            path: '/pagedeux',
            name: 'pagedeux',
            component: pagedeux
        },
        {
            path: '/listeinstallation',
            name: 'listeinstallation',
            component: listeinstallation
        },
        {
            path: '/elementvariable',
            name: 'elementvariable',
            component: elementvariable
        },
        {
            path: '/intervention',
            name: 'intervention',
            component: intervention
        }
    ],
    mode: 'history'
})


