<template >

  <v-card >
 
    
    




 

 <v-card-title class="soustitre">
        <i class="material-icons mr-2 roundicon">visibility</i> Vision détaillée du portefeuille
      </v-card-title>
      






     
    </v-card-title>


<v-layout row wrap class="mr-3 ml-3">

      <v-flex xs2 >
      <v-menu
        ref="menu"
        lazy
        :close-on-content-click="false"
        
        transition="scale-transition"
        offset-y
        full-width
        :nudge-right="40"
        min-width="290px"
        
