        <v-text-field
          slot="activator"
          label="Date de début"
          
          prepend-icon="event"
          readonly
        ></v-text-field>
        <v-date-picker v-model="picker"   scrollable>
          <v-spacer></v-spacer>
          <v-btn flat color="error" @click="menu = false">Cancel</v-btn>
          <v-btn flat color="success" @click="$refs.menu.save(date)">OK</v-btn>
        </v-date-picker>
      </v-menu>
    </v-flex>


    <v-flex xs2 >
      <v-menu
        ref="menu"
        lazy
        :close-on-content-click="false"
        
        transition="scale-transition"
        offset-y
        full-width
        :nudge-right="40"
        min-width="290px"
        
      >
        <v-text-field
          slot="activator"
          label="Date de fin"
          
          prepend-icon="event"
          readonly
        ></v-text-field>
        <v-date-picker v-model="picker2"  scrollable>
          <v-spacer></v-spacer>
          <v-btn flat color="error" @click="menu = false">Cancel</v-btn>
          <v-btn flat color="success" @click="$refs.menu.save(date)">OK</v-btn>
        </v-date-picker>
