    </v-flex>


    <v-flex xs2 class="pl-5">
       <v-select
          :items="selectlist"
          v-model="e1"
          label="Mois"
          single-line
        ></v-select>
    </v-flex>
<v-flex xs2>
</v-flex>
    <v-flex xs4>
       <v-text-field
        append-icon="search"
        label="Search"
        single-line
        hide-details
        v-model="search"
      ></v-text-field>
    </v-flex>
</v-layout>




  <v-data-table 
    :headers="headers"
    :items="items"
    :search="search"
    v-model="selected"
    item-key="name"
    select-all
    class="elevation-1"
  >
    <template slot="headerCell" slot-scope="props">
      <v-tooltip bottom>
        <span slot="activator">
          {{ props.header.text }}
        </span>
        <span>
          {{ props.header.text }}
        </span>
      </v-tooltip>
    </template>
    <template slot="items" slot-scope="props">
      <td>
        <v-checkbox
          primary
          hide-details
          v-model="props.selected"
        ></v-checkbox>
      </td>
      <td>{{ props.item.name }}</td>
      <td class="text-xs-right">{{ props.item.calories }}</td>
      <td class="text-xs-right">{{ props.item.fat }}</td>
      <td  class="text-xs-right">{{ props.item.carbs }}</td>
      <td class="text-xs-right">{{ props.item.protein }}</td>
      <td class="text-xs-right">{{ props.item.iron }}</td>
      <td class="text-xs-right"><v-icon color="green" >done</v-icon></td>
      <td class="text-xs-center">{{ props.item.consotot }}</td>
      <td style="background-color:#FF9800; width:50px" class="text-xs-center">{{ props.item.ecarttot }}</td>
