<template>
  <v-data-table
    :headers="headers"
    :items="items"
    hide-actions
  >
    <template slot="items" slot-scope="props">
      <td>  {{ props.item.name }}
          
      </td>
      <td  class="text-xs-center"><v-edit-dialog
          :return-value.sync="props.item.iron"
          large
          lazy
          persistent
        >
          <div>{{ props.item.fat }}</div>
          
          <v-text-field
            slot="input"
            label="Edit"
            v-model="props.item.iron"
            single-line
            counter
            autofocus
            :rules="[max20chars]"
          ></v-text-field>
        </v-edit-dialog></td>
      <td class="text-xs-center"><v-edit-dialog
          :return-value.sync="props.item.iron"
          large
          lazy
          persistent
        >
          <div>{{ props.item.fat }}</div>
          
          <v-text-field
            slot="input"
            label="Edit"
            v-model="props.item.iron"
            single-line
            counter
            autofocus
            :rules="[max20chars]"
          ></v-text-field>
        </v-edit-dialog></td>
      <td class="text-xs-center"><v-edit-dialog
          :return-value.sync="props.item.iron"
          large
          lazy
          persistent
        >
          <div>{{ props.item.carbs }}</div>
          
          <v-text-field
            slot="input"
            label="Edit"
            v-model="props.item.iron"
            single-line
            counter
            autofocus
            :rules="[max20chars]"
          ></v-text-field>
        </v-edit-dialog></td>
      <td class="text-xs-center"><v-edit-dialog
          :return-value.sync="props.item.iron"
          large
          lazy
          persistent
        >
          <div>{{ props.item.protein }}</div>
          
          <v-text-field
            slot="input"
            label="Edit"
            v-model="props.item.iron"
            single-line
            counter
            autofocus
            :rules="[max20chars]"
          ></v-text-field>
        </v-edit-dialog></td>
      <td class="text-xs-center">
        <v-edit-dialog
          :return-value.sync="props.item.iron"
          large
          lazy
          persistent
        >
          <div>{{ props.item.iron }}</div>
         
          <v-text-field
            slot="input"
            label="Edit"
            v-model="props.item.iron"
            single-line
            counter
            autofocus
            :rules="[max20chars]"
          ></v-text-field>
        </v-edit-dialog>
      </td>
      <td class="text-xs-center">
        <v-edit-dialog
          :return-value.sync="props.item.iron"
          large
          lazy
          persistent
        >
          <div>{{ props.item.samedi }}</div>
          
          <v-text-field
            slot="input"
            label="Edit"
            v-model="props.item.iron"
            single-line
            counter
            autofocus
            :rules="[max20chars]"
          ></v-text-field>
        </v-edit-dialog>
      </td>
      <td class="text-xs-center">
        <v-edit-dialog
          :return-value.sync="props.item.iron"
          large
          lazy
          persistent
        >
          <div>{{ props.item.dimanche }}</div>
         
          <v-text-field
            slot="input"
            label="Edit"
            v-model="props.item.iron"
            single-line
            counter
            autofocus
            :rules="[max20chars]"
          ></v-text-field>
        </v-edit-dialog>
      </td>
      <td class="text-xs-center">{{props.item.total}}</td>
    </template>
    
  </v-data-table>
</template>


<script>
  export default {
    data () {
      return {
        max20chars: (v) => v.length <= 20 || 'Input too long!',
       
        headers: [
          {
            text: '',
            align: 'left',
            value: 'name'
          },
          {text: 'lundi', value: 'calories'},
          {text: 'mardi', value: 'fat'},
          {text: 'marcredi', value: 'carbs'},
          {text: 'jeudi', value: 'protein'},
          {text: 'vendredi', value: 'iron'},
          {text: 'samedi', value: 'samedi'},
          {text: 'dimanche', value: 'dimanche'},
          {text: 'total', value: 'total'}
        ],
        items: [
          {
            value: false,
            name: 'Astreinte',
            calories: '__',
            fat: 6.0,
            carbs: '__',
            protein: 4.0,
            iron: '__',
            samedi:7,
            dimanche:7,
            total:30

          },
          {
            value: false,
            name: 'Heure progr.',
            calories: 237,
            fat: '__',
            carbs: '__',
            protein:'__',
            iron: '__',
            samedi:'__',
            dimanche:'__',
            total:0
          },
          {
            value: false,
            name: 'Dont heures de nuit',
            calories: '__',
            fat: '__',
            carbs: '__',
            protein: 6.0,
            iron: '__',
            samedi:7,
            dimanche:7,
            total:20
          },
          {
            value: false,
            name: 'Dont heures de jour férié',
            calories: '__',
            fat: '__',
            carbs: '__',
            protein: '__',
            iron: '__',
            samedi:7,
            dimanche:7,
            total:14
          },
         
        ]
      }
    }
  }
</script>



